.activity {
  min-height: 20rem;
  max-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $color-main-light;
  margin: 3rem;
  &__img {
    height: 10rem;
    animation: drive infinite 2s;
  }
}

@keyframes drive {
  0%,
  50%,
  100% {
    transform: translate(0, 0) rotate(0);
  }
  25% {
    transform: translate(0.6rem, 0.6rem) rotate(3deg);
  }
  75% {
    transform: translate(-0.6rem, 0.6rem) rotate(-3deg);
  }
}
